
.prod-Summary {
    @extend .regualr-body-m-16-;
    display: flex;
    justify-content: center;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    padding: 2em;
    

    li {
        @extend .regualr-body-m-16-;
        list-style-type: none;
    }
}

.price-val {
   
    @extend .regualr-body-m-16-;
    visibility: hidden;
    text-align: left;
}

.sign-in {
    margin-top: 2.5em;
    h4 {
        text-align: left;
    }
}

.sign-check {
    border: 1px solid #D1D3D6;
    border-radius: 2px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    background: var(--white) 0% 0% no-repeat padding-box;
}

.prod-price {
    height: 19rem;
    width: 26rem;
    padding: 2em;
    border: 1px solid #D1D3D6;
    border-radius: 2px;
    background: #FFFFFF 0% 0% no-repeat padding-box;
    background: var(--white) 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 8px #0000000D;


}

.sign-btn button {
    text-align: center;
    border-radius: 0.5rem;
    width: 111px;

}
